// src/components/heatmap-chart/heatmap-chart.tsx
import { Component, h, Element, Prop } from '@stencil/core';
import ApexCharts from 'apexcharts';

@Component({
  tag: 'heat-map',
  styleUrl: 'heat-map.css',
  shadow: true,
})
export class HeatmapChart {
  @Element() el!: HTMLElement;

  @Prop() rows: number = 7;
  @Prop() cols: number = 10;

  generateData(rows: number, cols: number) {
    return Array.from({ length: rows }, (_, r) => ({
      name: `Metric ${r + 1}`,
      data: Array.from({ length: cols }, (_, i) => ({
        x: `W${i + 1}`,
        y: Math.floor(Math.random() * 100),
      })),
    }));
  }

  componentDidLoad() {
    const chartEl = this.el.shadowRoot?.querySelector('#heatmap');
    if (!chartEl) return;

    const options: ApexCharts.ApexOptions = {
      chart: {
        type: 'heatmap',
        height: 350,
        toolbar: { show: false },
      },
      dataLabels: { enabled: false },
      colors: ['#008FFB'],
      series: this.generateData(this.rows, this.cols),
      plotOptions: {
        heatmap: {
          radius: 4,
          colorScale: {
            ranges: [
              { from: 0, to: 25, color: '#00A100', name: 'Low' },
              { from: 26, to: 50, color: '#128FD9', name: 'Medium' },
              { from: 51, to: 75, color: '#FFB200', name: 'High' },
              { from: 76, to: 100, color: '#FF0000', name: 'Extreme' },
            ],
          },
        },
      },
      title: {
        text: 'Heatmap Chart',
      },
    };

    const chart = new ApexCharts(chartEl, options);
    chart.render();
  }

  render() {
    return (
      <div>
        <ix-style-loader></ix-style-loader>
        <div id="heatmap" class="chart-container">

        </div>
      </div>
    );
  }
}
